import http from './http'

export function getDataPtnFilter(data) {
	return http.get(`master/ptn?nama=${data.nama}&kelompok=${data.kelompok}&prodi=${data.prodi}&extended=0`)
}

export function getDataPtn(data) {
	return http.get(`master/ptn`)
}

export function getDetailPtn(data) {
	return http.get(`master/ptn/${data}?extended=0`)
}

export function deleteDataPtn(id) {
	return http.delete('ptn/'+id)
}